import { createGlobalStyle } from "styled-components";

export const GlobalStyle = createGlobalStyle`
    /* 스타일 초기화 */
    html,
    body,
    div,
    span,
    applet,
    object,
    iframe,
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    p,
    blockquote,
    pre,
    a,
    abbr,
    acronym,
    address,
    big,
    cite,
    code,
    del,
    dfn,
    em,
    img,
    ins,
    kbd,
    q,
    s,
    samp,
    small,
    strike,
    strong,
    sub,
    sup,
    tt,
    var,
    b,
    u,
    i,
    center,
    dl,
    dt,
    dd,
    ol,
    ul,
    li,
    form,
    label,
    legend,
    table,
    caption,
    tbody,
    tfoot,
    thead,
    tr,
    th,
    td,
    article,
    aside,
    canvas,
    details,
    embed,
    figure,
    figcaption,
    footer,
    header,
    hgroup,
    menu,
    nav,
    output,
    ruby,
    section,
    summary,
    time,
    mark,
    audio,
    video {
    /* 전체 선택자 */
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 1em;
    font: inherit;
    vertical-align: baseline;
    }
    /* HTML5 display-role reset for older browsers */
    article,
    aside,
    details,
    figcaption,
    figure,
    footer,
    header,
    hgroup,
    menu,
    nav,
    section,
    main {
    display: block;
    }
    ul,
    ol {
    list-style: none;
    }
    table {
    border-collapse: collapse;
    border-spacing: 0;
    }
    /* 본문 스타일 */
    html {
    font-size: 10px;
    font-family: 'Noto Sans KR', sans-serif;
    }
    /* 하이퍼 링크 스타일 */
    a {
    color: inherit;
    text-decoration: none;
    }
    /* 기본 레이아웃 */
    *,
    *::before,
    *::after {
    box-sizing: border-box;
    }
    /* 숨김 */
    .readable-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    margin: -1px;
    clip: rect(0, 0, 0, 0);
    }

    img {
    width: 100%;
    }

    button {
    border: none;
    background-color: transparent;
    }

    input {
    border: none;
    }
`;
